import React, { Component } from 'react';
import {withStyles} from '@material-ui/core/styles'
import apiData from './api'
import DayLaneComponent from '../components/DayLaneComponent';
import { toast } from 'react-toastify';
import _ from 'lodash'

const styles = theme => ({
    root : {
        position : 'relative'
    },
    contadorTurnos : {
        width : 120,
        position : 'fixed',
        top : 80,
        right : 15
    },
    board : {
        display : 'inline-flex'
    },
    card : {
        background : 'blue'
    }
})

class Turnos extends Component {
    state = {
        data : apiData
    }
    takeTurn = (item)=>{
        const { data } = this.state;
        const {user,idDay,idCard} = item;
        let newData = _.cloneDeep(data);
        const indexLanes = _.findIndex(newData.lanes,x=>x.id === idDay);
        const indexCard = _.findIndex(newData.lanes[indexLanes].cards,x=>x.id === idCard);
        newData.lanes[indexLanes].cards[indexCard].users.push(user)
        this.setState({
            data : newData
        })
        toast("Turno tomado exitosamente! 👌")
    }
    render() {
        const {classes} = this.props;
        const { data } = this.state;
        return (
        <div className={classes.root}>
            <div className={classes.contadorTurnos}>
                {/* <ContadorTurnos /> */}
            </div>
            <div className={classes.board}>
                {data.lanes.map(x=>(
                    <DayLaneComponent {...x} takeTurn={this.takeTurn} />
                ))}
            </div>
        </div>
        );
  }
}

export default withStyles(styles)(Turnos);
