import React, { Component } from 'react';
import './App.css';
import { BrowserRouter as Router, Route,Switch,Redirect } from "react-router-dom";
import Dashboard from './theme/Dashboard'
import Turnos from './pages/Turnos'
import AdminUsers from './pages/AdminUsers'
import 'react-toastify/dist/ReactToastify.css';
import { ToastContainer } from 'react-toastify';

class App extends Component {
  render() {
    // const {classes} = this.props;
    return (
      <div>
          <Router basename="/app">
            <Dashboard>
                  <Switch>
                    <Route path="/" exact component={()=><Redirect to="/tomar_turnos" />} />
                    <Route path="/tomar_turnos" component={Turnos} />
                    <Route path="/admin_usuarios" component={AdminUsers} />
                    <Route component={()=><h1>Proximamente</h1>} />
                  </Switch>
            </Dashboard>
          </Router>
          <ToastContainer />
      </div>
    );
  }
}

export default App;
