import React from 'react'
import {Card, CardHeader,Typography} from '@material-ui/core'
import {withStyles} from '@material-ui/core/styles'
import CustomCard from '../CustomCard/CustomCard';

const styles = {
    root : {
      width : 200,
      padding : 5,
      margin : 3
    },
    header : {
        borderBottom: '1px solid #eee',
        padding : 5
    },
    title : {
        fontSize: 14, 
        fontWeight: 'bold',
        color : 'white',
        marginLeft : 5,
        marginRight : 5
    },
    takeTurnButton : {
        width : 25,
        height : 25,
        minHeight : 25,
        background : '#0879c9'
    }
}


class DayLaneComponent  extends React.Component {
    render(){
      const {classes,style,id : idDay,title,cards,takeTurn} = this.props;
        let totalUsers = 0;
        cards.forEach(card=>{
          card.users.forEach(user=>{
            totalUsers++;
          })
        })
        return (
          <Card className={classes.root} style={style}>
            <CardHeader 
            className={classes.header} 
            titleTypographyProps={{style : {display : 'inline-flex', justifyContent : 'space-between',width : '100%'}}}
            title={
              <React.Fragment>
                <Typography 
                className={classes.title}
                variant="body1">{title}</Typography>
                <Typography className={classes.title} color="inherit" variant="body1">{`${totalUsers}/15`}</Typography>
              </React.Fragment>}
            />
            {cards.map(card=>(
              <CustomCard {...card} idDay={idDay} takeTurn={takeTurn} />
            ))}
          </Card>
        )
    }
  }

export default withStyles(styles)(DayLaneComponent)