import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import {ListItemText, Typography, Grid} from '@material-ui/core';
import Collapse from '@material-ui/core/Collapse';
import {Link} from 'react-router-dom';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import HomeIcon from '@material-ui/icons/Home';
import AssignmentTurnedInIcon from '@material-ui/icons/AssignmentTurnedIn';
import SettingsIcon from '@material-ui/icons/Settings';
import AssignmentIcon from '@material-ui/icons/Assignment';

const icons = {
  home : HomeIcon,
  assignment_turned_in : AssignmentTurnedInIcon,
  settings : SettingsIcon,
  assignment : AssignmentIcon
}


const styles = theme => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  nested: {
    paddingLeft: theme.spacing.unit * 4,
  },

});

class MenuItem extends React.Component {
  state = { open: false };
  componentDidMount(){
    this.setState({ open : this.props.open})
  }
  handleClick = () => {
    this.setState({ open: !this.state.open });
  };
  render() {
    const { classes,title,subMenus,icon,path } = this.props;
    const Icon = icons[icon];
    return (
      <div className={classes.root}>
        <List
          component="nav"
        >
          <ListItem component={Link} to={path ? path : "#"} button onClick={this.handleClick}>
            <ListItemIcon>
              <Icon />
            </ListItemIcon>
            <ListItemText inset primary={title} />
            {subMenus.length > 0 && (
              this.state.open ? <ExpandLess /> : <ExpandMore />
            )}
          </ListItem>
          {subMenus.length > 0 &&
          <Collapse in={this.state.open} timeout="auto" unmountOnExit>
            <List component="div" disablePadding>
              {subMenus.map((item,i)=>(
                <ListItem component={Link} to={item.path} button key={i} className={classes.nested}>
                  <Grid container alignItems="center">
                    <Typography>{item.title}</Typography>
                  </Grid>
                </ListItem>
              ))}
            </List>
          </Collapse>
          }
        </List>
      </div>
    );
  }
}

MenuItem.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(MenuItem)
