import React from 'react'
import {Grid, Card, Avatar,ListItemAvatar,Typography, List, ListItem, ListItemText,Fab} from '@material-ui/core'
import {Add,Person} from '@material-ui/icons'
import {withStyles} from '@material-ui/core/styles'
import './custom-card.css'
import _ from 'lodash'

const styles = {
    root : {
      margin : 3
    },
    header : {
        borderBottom: '1px solid #eee',
        padding : 5
    },
    detail : {
      minHeight : 50
    },
    title : {
        fontSize: 14, 
        fontWeight: 'bold',
        color : 'black',
        marginLeft : 15
    },
    takeTurnButton : {
        width : 25,
        height : 25,
        minHeight : 25,
        background : '#0879c9'
    }
}

const CustomCard = props => {
    const {classes,idDay,id : idCard,title,users,takeTurn} = props;
    const myUser = {name : 'Roberto Araneda'};
    return (
      <Card className={classes.root}>
        <Grid className={classes.header} component="header" container alignItems="center" justify="space-between">
            <Typography className={classes.title} align="left">
                {title}
            </Typography>
            {(users.length !== 3  && !_.find(users,x=>x.name === myUser.name)) &&
            <Fab onClick={()=>takeTurn({user : myUser,idDay,idCard})} size="small" className={classes.takeTurnButton}>
              <Add style={{color : 'white'}} fontSize="small"/>
            </Fab>
            }
        </Grid>
        <Grid container alignItems="center" justify="center" className={classes.detail}>
            {users.length > 0 ? 
            <List dense>
              {users.map(user=>(
                <ListItem dense>
                  <ListItemAvatar >
                    <Avatar style={{width : 16,height : 16}} >
                      <Person style={{width : 12,height : 12}} />
                    </Avatar>
                  </ListItemAvatar>
                  <ListItemText
                    style={{fontSize : 12,fontWeight : 'bold'}}
                    primary={user.name}
                  />
                </ListItem>
              ))}
            </List> :
            <Typography variant="caption" align="center">
                {"No hay personas en este turno"}
            </Typography>
            }
        </Grid>
      </Card>
    )
  }

export default withStyles(styles)(CustomCard)